@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  font-display: swap;
  src: url(/istanbul-crypto/static/media/Inter-Regular.d22a0d66.woff2) format("woff2"),
       url(/istanbul-crypto/static/media/Inter-Regular.4d127124.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(/istanbul-crypto/static/media/Inter-Medium.027d14e7.woff2) format("woff2"),
       url(/istanbul-crypto/static/media/Inter-Medium.d1f6b6e9.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  font-display: swap;
  src: url(/istanbul-crypto/static/media/Inter-Black.49c59986.woff2) format("woff2"),
       url(/istanbul-crypto/static/media/Inter-Black.af718f91.woff) format("woff");
}

.page {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  background: #000000;
  color: #FFFFFF;
  margin: 0 auto;
  height: 100%;
  min-height: 100vh;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.page a {
  text-decoration: none;
  color: inherit;
}

.header {
  max-width: 880px;
  margin: 0 auto;
  border-bottom: 1px solid rgb(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 880px) {
  .header {
    padding: 29px 26px 13px;
  }
}

.header__logo {
    display: block;
    background: url(/istanbul-crypto/static/media/logo.511e050a.png) no-repeat;
    background-size: 150px;
    max-width: 130px;
    margin-left: 8px;
    width: 100%;
    height: 150px;
  }
  
  @media screen and (max-width:600px) {
    .header__logo {
      background-size: 104px 25px;
    }
  }
.header__menu {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  list-style-type: none;
  cursor: pointer;
}
.header__menu-item {
  width: 150px;
  text-align: center;
}

.header__button {
  width: 30px;
  height: 30px;
  padding: 0;
  margin-right: 30px;
  cursor: pointer;
}
.content {
  margin: 0 auto;
  min-height: 50vh;
  font-family: 'Inter';
}

@media screen and (max-width: 880px) {
  .content {
    margin: 0 19px;
  }
}

.content__section {
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding-top: 30px;
  background: url(/istanbul-crypto/static/media/gold.cab5bb45.webp) round;
}

.content__section_gold {
  background: url(/istanbul-crypto/static/media/goldd.949d2810.jpeg) round;
}

.content__section_crypto {
  background: url(/istanbul-crypto/static/media/crypto.0822e32c.jpg) round;
}


.content__section_coins {
  background: url(/istanbul-crypto/static/media/coins.23cfb4ee.jpg) round;
}
.content__text {
  display: flex;
  flex-direction: column;
  height: 100px;
}

.content__title {
  height: 200px;
  font-size: 20px;
  padding-top: 20px;
}


.content__link {
  text-decoration: none;
  background-color: transparent;
  color: white;
}

.footer {
  color: #545454;
  max-width: 880px;
  margin: 0px auto;
  padding: 62px 0 34px;
  font-size: 19px;
  line-height: 1.22;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 880px) {
  .footer {
    font-size: 14px;
    line-height: 17px;
    padding: 48px 19px 36px;
  }
}

.footer__group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer__item {
  color: #545454;
  font-size: 19px;
  line-height: 1.22;
}




