.footer {
  color: #545454;
  max-width: 880px;
  margin: 0px auto;
  padding: 62px 0 34px;
  font-size: 19px;
  line-height: 1.22;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 880px) {
  .footer {
    font-size: 14px;
    line-height: 17px;
    padding: 48px 19px 36px;
  }
}
